@import "./assets/css/reset.css";
@import "./assets/css/animations.css";

@font-face {
  font-family: alef;
  src: url(./assets/fonts/Alef-Regular.ttf);
}
@font-face {
  font-family: alef-bold;
  src: url(./assets/fonts/Alef-Bold.ttf);
}
@font-face {
  font-family: numbers-font;
  src: url(./assets/fonts/Signika-Regular.ttf);
}
@font-face {
  font-family: numbers-font-light;
  src: url(./assets/fonts/Signika-Light.ttf);
}
.App {
  background-color: #f9f6f7;
  min-height: 100vh;
  * {
    box-sizing: border-box;
    font-family: alef, sans-serif;
    color: #484848;
  }
  img {
    width: 100%;
    height: 100%;
  }
  a{
    cursor: pointer;
  }
  .homepage {
    .searchfield-container {
      width: 250px;
      margin: 0 auto;
      margin-bottom: 40px;
    }
    .card-container {
      width: 100%;
      padding-top: 12px;
      padding-bottom: 50px;
      .wrapper {
        max-width: 860px;
        margin: 0 auto;
        .main-day-card {
          width: 100%;
          background-color: white;
          height: 500px;
          margin-bottom: 20px;
          display: flex;
          .right-section {
            padding: 24px;
            width: 540px;
            .container {
              width: 100%;
              .top-section {
                display: flex;
                justify-content: space-between;
                .side-buttons {
                  display: flex;
                  justify-content: space-between;
                  width: 122px;
                  text-decoration: underline;
                }
              }
              h2 {
                margin-bottom: 10px;
              }
              .row-line {
                width: 100%;
                border-top: 1px solid #c1c1c1;
              }
              .day-statistics {
                padding: 42px 20px;
                display: flex;
                justify-content: space-between;
                margin: 0 auto;
                max-width: 460px;
                .day-statistics-el {
                  h5{
                    text-align: center;
                  }
                  .font-awsome-container {
                    font-size: 30px;
                    display: flex;
                    justify-content: center;
                    path {
                      color: #ffb74d;
                    }
                    margin-bottom: 20px;
                  }
                }
              }
            }
            .week-forcast {
              margin: 0 auto;
              max-width: 424px;
              padding-top: 42px;
              li {
                display: flex;
                justify-content: space-between;
                margin-bottom: 30px;

                span {
                  display: flex;
                  p {
                    margin-right: 30px;
                  }
                  h4 {
                    display: flex;
                    font-size: 24px;
                    color: #ffb74d;
                    margin-top: -8px;
                    width: 27px;
                    span {
                      color: #ffb74d;
                      font-size: 16px;
                    }
                    .fahrenheit-symbol {
                      margin-top: 8px;
                      margin-left: 5px;
                    }
                  }
                }
              }
            }
          }
          .left-section {
            border-radius: 10px 0 0 10px;
            width: 320px;
            height: 100%;
            padding: 24px;
            background: #e65c00; /* fallback for old browsers */
            background: -webkit-linear-gradient(
              to left,
              #f9d423,
              #e65c00
            ); /* Chrome 10-25, Safari 5.1-6 */
            background: linear-gradient(
              to left,
              #f9d423,
              #e65c00
            ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .date-section {
              h2 {
                * {
                  color: white;
                }
                color: white;
              }
            }
            .bottom-section {
              .temperature-number {
                display: flex;
                margin-bottom: 30px;
                h3 {
                  font-size: 144px;
                  color: white;
                }
                span {
                  margin-top: 25px;
                  font-size: 80px;
                  color: white;
                }
                .fahrenheit-symbol {
                  display: flex;
                  align-items: flex-end;
                }
              }
              h4,
              h5 {
                color: white;
              }
              h4 {
                margin-bottom: 15px;
              }
            }
          }
        }
      }
    }

    .img-cover {
      background-image: url("https://res.cloudinary.com/explority/image/upload/v1578647086/ws_Winter_Snow_1600x1200_dm9qtq.jpg");
      width: 100%;
      background-attachment: fixed;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 480px;
      display: flex;
      align-items: center;
      h1 {
        margin: 0 auto;
        display: inline-flex;
        font-size: 60px;
        color: white;
        font-weight: bold;
      }
    }
  }
  .navbar {
    background-color: none;
    .navbar-container {
      max-width: 1200px;
      margin-right: 10px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      .logo {
        display: flex;
        .img-container {
          width: 70px;
          height: 70px;
        }
        h1 {
          display: inline-flex;
          font-size: 30px;
          font-family: alef-bold;
          margin: 5px 0;
          padding-top: 18px;
        }
      }
      .nav-links {
        display: flex;
        align-items: flex-end;
        margin-bottom: 14px;

        a {
          padding: 10px;
          text-decoration: none;
          color: #484848;
        }
      }
    }
  }
}
@media (max-width: 680px) {
  .App .homepage .card-container .wrapper .main-day-card {
    display: block !important;
    .left-section {
      width: 100% !important;
      border-radius: unset;
      height: 400px!important;
      .bottom-section {
        display: flex !important;
        flex-direction: column !important;
        align-items: center !important;
      }
    }
    .right-section {
      background-color: white !important;
      margin: 0 auto !important;
      width: 100% !important;
    }
  }
}
@media (max-width: 420px) {
  .App .homepage .card-container .wrapper .main-day-card .right-section {
    padding: 8px !important;
    .container .day-statistics {
      padding: 42px 8px !important;
    }
    .week-forcast li span .weather-text {
      display: none !important;
    }
  }

  .App .navbar .navbar-container .logo h1 {
    display: none;
  }
}

// animation classes

.slide-in-fwd-center {
	-webkit-animation: slide-in-fwd-center 0.3s both;
	        animation: slide-in-fwd-center 0.3s both;
}